const scrollOptionFactory = {
    create(context, options, eventHandlers) {
        let opt = {
            options: {
                enableDrop: false,
                data() { return null; }
            },
            eventHandlers: {
                onDragStart(e, data, dragParams) {},
                onDragEnd(e, data, dragParams) {},
                onDragEnter(e, data, dragParams) {},
                onDragLeave(e, data, dragParams) {},
                onDrag(e, data, dragParams) {},
                onDrop(e, data, dragParams) {}
            }
        };

        if (options) opt.options = Object.assign({}, opt.options, options);
        if (eventHandlers) opt.eventHandlers = Object.assign({}, opt.eventHandlers, eventHandlers);

        return opt;
    }
};

const getDragParams = function(el, e) {
    return { gg: 11 };
};

const bindDragEventHandler = function({ context, el, dragOption }) {
    if (!el || !dragOption) return;

    let handler = dragOption.eventHandlers, options = dragOption.options;

    if (dragOption.options.enableDrop) {
        el.ondragover = (e) => {   
            // el.style.border = '2px solid #409EFF';
            
            e.preventDefault(); 
            e.stopPropagation(); 
            return false; 
        };
        el.ondrop = (e) => { 
            handler.onDrop.call(context, e, JSON.parse(e.dataTransfer.getData('data')), getDragParams(el, e));
            e.stopPropagation();
            return false;
        };

    } else {        
        el.ondragstart = (e) => {            
            e.dataTransfer.setData('data', JSON.stringify(options.data()));
            handler.onDragStart.call(context, e, options.data(), getDragParams(el, e));
        };
        el.ondragend = (e) => { 
            handler.onDragEnd.call(context, e, options.data(), getDragParams(el, e)); 
        };
        el.ondragenter = (e) => { 
            handler.onDragEnter.call(context, e, options.data(), getDragParams(el, e)); 
        };
        el.ondragleave = (e) => { 
            handler.onDragLeave.call(context, e, options.data(), getDragParams(el, e)); 
        };
        el.ondrag = (e) => { 
            handler.onDrag.call(context, e, options.data(), getDragParams(el, e)); 
        };

        el.ondragover = null;
        el.ondrop = null;
    }
}


/**
 * @description dom元素拖动操作封装指令
 * 
 * 指令： v-datadrag
 * 
 * 参数说明:
 * ======================================================================================================
 *  disable 
 *      用法 -> v-drag:disable
 *      说明 -> 设置绑定元素是否禁用编辑
 * 
 * 不传默认为空
 * 
 * 
 * 修饰符说明:
 * ======================================================================================================
 * 
 *  
 *  drop
 *      用法 -> v-drag.drop
 *      说明 -> 若添加此修饰，代表支持其他元素进行拖入，会对其他元素的拖入事件监听
 * 
 * 
 * 
 * 
 * 
 * 
 */
const datadrag = {
    inserted(el, binding, vnode, oldVnode) {
        const context = vnode.context;
        let argument = binding.arg;
        let mdf_drop = Boolean(binding.modifiers['drop']);

        if (!context) return;


        if (argument === 'disable') {
            el.setAttribute('draggable', false);
            return;
        }
        el.setAttribute('draggable', true);


        let data = null;
        let eventHandlers = {};

        switch (typeof(binding.value)) {
            case 'string':
            case 'number':
            case 'boolean':
                data = binding.value;
                break;
            
            case 'function':
                if (mdf_drop) {
                    eventHandlers.onDrop = binding.value;
                } else {
                    eventHandlers.onDragEnd = binding.value;
                }
                break;

            case 'object':
                data = binding.value.data;
                eventHandlers = binding.value.handlers || {};
                break;
        }
        
        let dragOption = scrollOptionFactory.create(context, { 
            enableDrop: mdf_drop, 
            data() { return data != null ? data : ''; } 
        }, eventHandlers);
        bindDragEventHandler({ context, el, dragOption });
    }
};

export default datadrag;


