import { ElementMixin, HandleDirective } from 'vue-slicksort';

export default {
    mixins: [ElementMixin],
    directives: { handle: HandleDirective },
    props: {
        /** 渲染的标签类型 */
        tag: { type: String, default: 'div' }
    },
    render(h) {
        return h(this.tag, {
            props: Object.assign({}, this.$attrs, this.$options.propsData),
            on: this.$listeners
        }, this.$slots.default);
    }
}


