import XEUtils from 'xe-utils';

var mixin = {
    data() {
        return {
            defaultAll:false
        };
    },    
    methods: {
        // 树
        queryTradeTree(customerId,projectId) {

            this.treeLoading = true;
            return this.$sacpApis
                .queryTradeTree({ param1: customerId||this.userinfo.curCustomerId, param2: projectId||this.userinfo.curProjectId })
                .then((res) => {
                    if (res.success) {
                        this.treeLoading = false;
                        if (res.data && res.data.length) {
                            res.data[0].children = Object.values(
                                res.data[0].children
                            );
                            this.treeData = (res.data || []).map((item)=>{
                                item.children = (item.children || []).map((i)=>{
                                    let regex = /\((.+?)\)/g;
                                    let options = i.classifyName.match(regex)
                                    let option = options[0]
                                    let result=option.substring(1, option.length - 1)
                                    if(result>0){
                                        this.defaultAll=true;
                                    }
                                    return i;
                                })
                                return item;
                            });
                            this.onTreeDataUpdate && this.onTreeDataUpdate(this.treeData);                            

                        } else {
                            this.treeData = [];                            
                        }
                        return this.treeData;

                    } else {
                        return this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        // 合并单元格
        mergeTable(block) {
            if (Array.isArray(block) && block.length) {
                const data = block[0].data;
                data.forEach((x) => {
                    x.rowspan = null;
                });
                const id = data.map((x) => {
                    if (x.mergeCustomerName == '小计') {
                        x.rowspan = 1;
                    }
                    return x.mergeCustomerName;
                });
                id.forEach((x, idx) => {
                    if (x !== '小计') {
                        if (id.indexOf(x) === id.lastIndexOf(x)) {
                            data[idx].rowspan = 1;
                        } else {
                            data[id.lastIndexOf(x)].rowspan =
                                id.lastIndexOf(x) - id.indexOf(x) + 1;
                        }
                    }
                });
                return block;
            }
        },
        tableSum(block) {
            // debugger
            if (Array.isArray(block) && block.length) {
                const sumRow = {
                    bgc: true,
                };
                block.forEach((x, idx) => {
                    if (idx !== 0) {
                        const children = x.columns[0].children;
                        if (children) {
                            children.forEach((l, lx) => {
                                let sum = '';
                                if (l.pageFieldType === '1') {
                                    x.data.forEach((j, jx) => {
                                        if (!j.sum && j.uscc) {
                                            sum = XEUtils.add(
                                                sum * 1,
                                                j[l.prop]
                                            );
                                        }
                                    });
                                }
                                sumRow[l.prop] = sum;
                            });
                        }
                    }
                });
                const data0 = block[0].data;
                const data1 = block[1].data;
                if (data0.length === data1.length) {
                    if (
                        data0.length &&
                        data1.length &&
                        !data1[data1.length - 1].bgc
                    ) {
                        data0.push({ mergeCustomerName: `合计` });
                        data1.push(sumRow);
                    } else {
                        data0[data0.length - 1] = { mergeCustomerName: `合计` };
                        data1[data1.length - 1] = sumRow;
                    }
                }
            }
            return block;
        },
    },
};

export { mixin };



