<template>
    <div
        class="bdo__gridmocktable ant-table-body"
        :style="{ width: width, height: height }"
        @dragleave="onDragleave"
    >
        <drag-container
            v-model="tableBlocks"
            tag="div"
            axis="x"
            lock-axis="x"
            helper-class="bdo__gridmocktable--item-hover"
            :class="['bdo__gridmocktable--container', { border: border }]"
            :use-drag-handle="!colDragable"
            @sort-end="onItemSortEnd"
            @dragleave="onDragleave"
        >
            <!-- 可拖动的表格区块 -->
            <drag-item
                tag="div"
                v-for="(blockItem, blockIndex) in tableBlocks"
                :key="'block_' + blockIndex"
                :index="blockIndex"
                :item="blockItem"
                :value="blockItem"
                :class="[
                    'bdo__gridmocktable--block',
                    {
                        border: border,
                        active: activeBlockIndex === blockIndex,
                    },
                ]"
                :data-dropflag="blockIndex"
                @dragover.prevent="onDragover"
                @dragenter="onDragenter"
                @dragleave="onDragleave"
                @drop="onDrop($event)"
            >
                <table class="bdo__gridmocktable--block-table">
                    <thead class="block-header dragable" title="长按拖动区块">
                        <tr
                            v-for="(
                                headerRowItems, headerRowIndex
                            ) in getHeaderColumnRenderData(blockItem.columns)"
                            :key="'header_row_' + headerRowIndex"
                            :class="['block-header-row', { border: border }]"
                        >
                            <th
                                v-for="(colItem, colIndex) in headerRowItems"
                                :key="`${headerRowIndex}-${colIndex}`"
                                :colspan="colItem.colspan"
                                :rowspan="colItem.rowspan"
                                :class="[
                                    'bdo__gridmocktable--header-column',
                                    { border: border },
                                ]"
                                :width="colItem.colWidth ||  colMinWidth"
                                :style="{
                                    height: rowHeight,
                                    lineHeight: rowLineHeight,
                                    minWidth: colMinWidth,
                                    position: 'relative'
                                }"
                                :data-dropflag="blockIndex"
                            >
                                <slot :name="`col-header-${colItem.prop}`">
                                    <a-icon
                                        type="edit"
                                        style="margin-left: 3px"
                                        v-if="colItem.isEdit == 1 && !editIcon"
                                    />
                                </slot>
                                {{ colItem.title }}
                                <span class="resize-handler"></span>
                            </th>
                        </tr>
                        <!-- 占位行 -->
                        <tr
                            v-for="_item in calcLeftHeaderRows(
                                blockItem.columns
                            )"
                            :key="_item"
                            :class="['block-header-row', { border: border }]"
                            :data-dropflag="blockIndex"
                        ></tr>
                    </thead>
                    <drag-container
                        tag="tbody"
                        v-model="blockItem.data"
                        class="bdo__gridmocktable--block-wrapper"
                        lock-axis="y"
                        helper-class="bdo__gridmocktable--item-hover"
                        :use-drag-handle="!rowDragable"
                        @sort-end="onItemSortEnd2"
                    >
                        <!-- 可拖动的表格行 -->
                        <drag-item
                            tag="tr"
                            :class="[
                                'bdo__gridmocktable--row',
                                { border: border },
                                rowItem.bgc ? 'autoFill' : '',
                                rowItem.rowspan === null ? 'merge' : '',
                            ]"
                            :style="{
                                height:
                                    rowItem.rowspan == null
                                        ? '0px'
                                        : mergeRowHeight(rowItem),
                            }"
                            v-for="(rowItem, rowIndex) in blockItem.data"
                            :key="'row_' + rowIndex"
                            :index="rowIndex"
                            :item="rowItem"
                            :value="rowItem"
                            :data-dropflagrow="rowIndex"
                            :draggable="rowDragableState"
                            @dragover.prevent="onDragoverRow(rowIndex)"
                            @dragenter="onDragenterRow(rowIndex)"
                            @dragleave="onDragleaveRow(rowIndex)"
                            @drop="dropRow($event, rowIndex)"
                            @dragstart="onDragStartRow(rowIndex)"
                            @contextmenu.prevent="
                                delecteRows(
                                    $event,
                                    rowItem,
                                    rowIndex,
                                    blockIndex
                                )
                            "
                        >
                            <td
                                :title="
                                    rowItem[colItem.prop]
                                        | capitalize(colItem.pageFieldType)
                                "
                                class="bdo__gridmocktable--column"
                                v-for="(
                                    colItem, colIndex
                                ) in getPropRenderColumns(blockItem.columns)"
                                :key="`${rowIndex}-${colIndex}`"
                                :class="[
                                    'bdo__gridmocktable--column',
                                    { border: border },
                                ]"
                                :style="{
                                    height: rowHeight,
                                    lineHeight: rowLineHeight,
                                    minWidth: colMinWidth,
                                    textAlign: !isNaN(rowItem[colItem.prop])
                                        ? 'right'
                                        : 'left',
                                }"
                                :data-dropflag="blockIndex"
                            >
                                <slot
                                    :name="`col-${colItem.prop}`"
                                    :row="rowItem"
                                    :col="rowItem[colItem.prop]"
                                    :curCol="colItem.prop"
                                    :columns="colItem"
                                    :colIndex="blockIndex"
                                    :rowIndex="rowIndex"
                                    :record="blockItem.data"
                                    :recordCol="blockItem.columns"
                                    :cellIndex="colIndex"
                                >
                                    {{
                                        rowItem[colItem.prop]
                                            | capitalize(colItem.pageFieldType)
                                    }}
                                </slot>
                            </td>
                        </drag-item>
                        <!-- 占位行 -->
                        <tr
                            v-for="_item in calcLeftRows(blockItem)"
                            :key="_item"
                            :class="[
                                'bdo__gridmocktable--row bos',
                                { border: border },
                            ]"
                        >
                            <td
                                v-for="(
                                    colItem, colIndex
                                ) in getPropRenderColumns(blockItem.columns)"
                                :key="colIndex"
                                :class="[
                                    'bdo__gridmocktable--column',
                                    { border: border },
                                ]"
                                :style="{
                                    height: rowHeight,
                                    lineHeight: rowLineHeight,
                                }"
                                :data-dropflag="blockIndex"
                            ></td>
                        </tr>
                    </drag-container>
                </table>
            </drag-item>
        </drag-container>
        <button
            v-show="deleteIcon"
            :style="{ position: 'fixed', top: isTop, left: isLeft }"
            @click="deleteRowData"
        >
            <a-icon type="delete" class="deleteRowIcon" />
        </button>
    </div>
</template>

<script>
import DragContainer from './DragContainer';
import DragItem from './DragItem';
import datadrag from './datadrag';
// import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import XEUtils from 'xe-utils';
import { mixin } from '../pages/insideTransactionSummary/mixins/drageTable';
export { datadrag };

export default {
    components: { DragContainer, DragItem },
    props: {
        value: Array,
        /** 是否显示边框 */
        border: Boolean,
        /** 表格宽度 */
        width: { type: String, default: 'auto' },
        /** 表格高度 */
        height: { type: String, default: 'auto' },
        /** 行高 */
        rowHeight: { type: String, default: '32px' },
        /** 文字行高 */
        rowLineHeight: { type: String, default: '30px' },
        /** 每列的最小宽度 */
        colMinWidth: { type: String, default: '100px' },
        /** 是否开启水平拖动 */
        colDragable: { type: Boolean, default: true },
        /** 是否开启垂直拖动 */
        rowDragable: { type: Boolean, default: true },
        /** 是否开启垂直拖动(原先的不起效) */
        rowDragableState: { type: Boolean, default: true },
        // 控制表头图标
        editIcon: { type: Boolean, default: false },
    },
    mixins: [mixin],
    data() {
        return {
            tableBlocks: [],
            activeBlockIndex: null,
            deleteIcon: false,
            isTop: '',
            isLeft: '',
            curRow: {},
            rowIndex: 0,
            columnsIndex: 0,
            deleteCurRow: {},
            activeRowIndex: null,
            rightData: '',
            activeRowStartIndex: '',
        };
    },
    computed: {
        tableBlockMaxRows() {
            if (!this.tableBlocks || this.tableBlocks.length === 0) {
                return 0;
            }

            let maxRows = [];
            this.tableBlocks.forEach((item) => {
                if (Array.isArray(item.data)) {
                    maxRows.push(item.data.length);
                }
            });
            return Math.max(...maxRows);
        },
        tableBlockHeaderMaxRows() {
            if (!this.tableBlocks || this.tableBlocks.length === 0) {
                return 0;
            }

            let maxRows = [];
            this.tableBlocks.forEach((item) => {
                if (Array.isArray(item.columns)) {
                    maxRows.push(this.clacTreeStructLevels(item.columns, 0));
                }
            });

            return Math.max(...maxRows);
        },
        field() {
            return this.$store.state.table.field;
        },
    },
    watch: {
        value: {
            handler(val) {
                this.tableBlocks = val || [];
            },
            immediate: true,
            deep: true,
        },
    },
    filters: {
        capitalize: (value, type) => {
            if (type === '1') {
                if (value) {
                    return formatter.commafy(value * 1);
                } else {
                    return '';
                }
            } else if (value) {
                return value;
            } else {
                return '';
            }
        },
    },
    methods: {
        // 计算树结构的最大深度
        clacTreeStructLevels(treeArr, initLevel) {
            if (!Array.isArray(treeArr)) {
                return initLevel;
            }

            let tmpLvs = [];
            treeArr.forEach((item) => {
                if (Array.isArray(item.children)) {
                    tmpLvs.push(
                        this.clacTreeStructLevels(item.children, initLevel + 1)
                    );
                } else {
                    tmpLvs.push(initLevel + 1);
                }
            });

            return Math.max(...tmpLvs);
        },
        getPropRenderColumns(columns) {
            let rst = [];
            this.calcRenderedEndNodeColumns(rst, columns);
            return rst;
        },
        // 获取最终和数据相关联的字段信息
        calcRenderedEndNodeColumns(result, columns) {
            if (!Array.isArray(columns)) return;

            columns.forEach((col) => {
                if (!Array.isArray(col.children) || col.children.length === 0) {
                    result.push(col);
                } else {
                    this.calcRenderedEndNodeColumns(result, col.children);
                }
            });
        },
        // 计算表头默认的colspan和rowspan
        calcColumnSpanInfo(colItem, currentRow) {
            if (!colItem) return colItem;

            if (!Array.isArray(colItem.children)) {
                colItem.colspan = colItem.colspan || 1;
                colItem.rowspan =
                    colItem.rowspan ||
                    this.tableBlockHeaderMaxRows - currentRow ||
                    1;
            } else {
                let cs = 0,
                    rs = 0;
                for (let i = 0; i < colItem.children.length; i++) {
                    this.calcColumnSpanInfo(
                        colItem.children[i],
                        currentRow + 1
                    );
                    cs += colItem.children[i].colspan;
                }
                colItem.colspan = colItem.colspan || cs || 1;
                colItem.rowspan = colItem.rowspan || 1;
            }
        },
        // 将表头信息由树结构转换为Map结构（key为 rowIndex）
        parseColumnRowData(rowData, columns, currentRow) {
            if (!columns || !Array.isArray(columns)) return [];

            for (let i = 0; i < columns.length; i++) {
                if (rowData.hasOwnProperty(currentRow.toString())) {
                    rowData[currentRow.toString()].push(columns[i]);
                } else {
                    rowData[currentRow.toString()] = [columns[i]];
                }

                if (Array.isArray(columns[i].children)) {
                    this.parseColumnRowData(
                        rowData,
                        columns[i].children,
                        currentRow + 1
                    );
                }
            }
        },
        // 获取可以渲染表头的数据结构
        getHeaderColumnRenderData(columns) {
            if (!columns || !Array.isArray(columns)) return [];

            let cols = columns,
                rowData = {};
            for (let i = 0; i < cols.length; i++) {
                this.calcColumnSpanInfo(cols[i], 0);
            }

            this.parseColumnRowData(rowData, cols, 0);

            // 将表头信息由树结构转换为二维数组结构
            let rowIndexList = Object.keys(rowData)
                .map((item) => Number(item))
                .sort();
            return rowIndexList.map((idx) => rowData[idx.toString()]);
        },
        // 计算不同table区块需要填充空白的行数
        calcLeftRows(blockItem) {
            if (!blockItem || !Array.isArray(blockItem.data)) return 0;

            return this.tableBlockMaxRows - blockItem.data.length;
        },
        // 计算不同table区块需要填充空白的表头行数
        calcLeftHeaderRows(columns) {
            if (!Array.isArray(columns)) return 0;

            let renderRows = this.getHeaderColumnRenderData(columns);
            return this.tableBlockHeaderMaxRows - renderRows.length;
        },
        onItemSortEnd({ event, newIndex, oldIndex, collection }) {
            console.log('onItemSortEnd', event, newIndex, oldIndex, collection);
            this.$emit('input', this.tableBlocks);
        },
        onItemSortEnd2({ event, newIndex, oldIndex, collection }) {
            console.log(
                'onItemSortEnd2',
                event,
                newIndex,
                oldIndex,
                collection
            );
            this.$emit('input', this.tableBlocks);
        },
        onDragover(e) {
            // console.log('e', e);
            if (e.target.dataset.dropflag) {
                this.activeBlockIndex = Number(e.target.dataset.dropflag);
            }
        },
        onDragenter(e) {
            if (e.target.dataset.dropflag) {
                this.activeBlockIndex = Number(e.target.dataset.dropflag);
            }
        },
        onDragleave(e) {
            if (!e.target.dataset.dropflag) {
                this.activeBlockIndex = null;
            }
        },
        // 外部数据拖动到表格区块的时候
        onDrop(e) {
            if (this.activeBlockIndex != null) {
                let data = e.dataTransfer.getData('data');
                data = data ? JSON.parse(data) : data;
                this.rightData = data;
                this.$emit(
                    'data-drop',
                    this.activeBlockIndex,
                    data,
                    this.activeRowIndex
                );
            }
            this.activeBlockIndex = null;
        },
        onDragoverRow(rowIndex) {
            // console.log('rowIndex',rowIndex)
            this.activeRowIndex = rowIndex;
        },
        onDragenterRow(rowIndex) {
            this.activeRowIndex = rowIndex * 1;
        },
        onDragleaveRow(rowIndex) {
            this.activeRowIndex = null;
        },
        onDragStartRow(rowIndex) {
            // console.log(rowIndex,this.activeRowIndex)
            this.activeRowStartIndex = rowIndex;
        },
        dropRow(e, rowIndex) {
            // rowIndex：目标行，colIdx：当前列,this.activeRowStartIndex:开始行
            const colIdx = e.target.dataset.dropflag;
            const customerData = this.tableBlocks[0].data;
            const startRow = customerData[this.activeRowStartIndex];
            if (colIdx && startRow) {
                const data = this.tableBlocks[colIdx * 1].data;
                if (customerData[rowIndex].uscc === startRow.uscc) {
                    data[rowIndex] = data.splice(
                        this.activeRowStartIndex,
                        1,
                        data[rowIndex]
                    )[0];
                } else {
                    this.$message.error('统一信用代码不同拖拽无效！');
                }
            }
        },
        delecteRows(e, row, rowIndex, columnsIndex) {
            // console.log(row, rowIndex, columnsIndex)
            return;
            if (row && row.uscc && !this.editIcon) {
                this.deleteIcon = true;
                // console.log('e', e, row);
                this.isTop = e.pageY + 'px';
                this.isLeft = e.pageX + 'px';
                this.curRow = row;
                this.rowIndex = rowIndex;
                this.columnsIndex = columnsIndex;
            } else {
                this.deleteIcon = false;
            }
            window.addEventListener('click', (e) => {
                this.deleteIcon = false;
                // console.log(e)
            });
        },
        deleteRowData() {
            // debugger;
            // 删除分两种情况，删除某一大列和删除全部的列
            const table = this.tableBlocks[this.columnsIndex];
            let deleteCurData = table.data;
            this.deleteCurRow = deleteCurData[this.rowIndex];
            this.deleteIcon = false;
            if (!this.deleteCurRow.uscc) return;
            delete this.deleteCurRow.bgc;
            this.$emit('deleteData', this.deleteCurRow, this.columnsIndex);
            //还原回拖拽前(初始化)
            // deleteCurData[this.rowIndex] = this.field[this.columnsIndex];
            this.$set(
                deleteCurData,
                this.rowIndex,
                this.field[this.columnsIndex]
            );
            let be = false;
            // 判断该行的其他大列是否有值
            this.tableBlocks.forEach((x, idx) => {
                if (idx != 0 && idx != this.columnsIndex) {
                    const col = x.columns[0];
                    // 获取表头字段
                    if (col.children && col.children.length) {
                        for (let i = 0; i < col.children.length; i++) {
                            if (x.data[this.rowIndex][col.children[i].prop]) {
                                be = true;
                            }
                        }
                    } else if (x.data[this.rowIndex][col.prop]) {
                        be = true;
                    }
                }
            });

            if (!be) {
                this.tableBlocks.forEach((x) => {
                    x.data.splice(this.rowIndex, 1);
                });
            }
            this.tableBlocks = this.tableSum(this.tableBlocks);
        },
        mergeRowHeight(rowItem) {
            const height = this.rowHeight.split('px')[0];
            return `${rowItem.rowspan * height}px`;
            // if (rowItem.rowspan > 1) {
            //     return `${rowItem.rowspan * height + rowItem.rowspan}px`;
            // } else {
            //     return `${rowItem.rowspan * height}px`;
            // }
        },
    },
};
</script>

<style lang="scss">
.resize-handler {
    position: absolute;
    top: 0;
    right: -1px;
    bottom: 0;
    width: 4px;
    font-size: 0;

    &:hover {
        background-color: #000;
        cursor: e-resize;
    }
}
.merge {
    width: auto;
    height: auto;
    border-bottom: 0px solid !important;
    td {
        height: 0px !important;
        line-height: 0px !important;
    }
}

.autoFill {
    // background-color: #04f80447;
    background-color: #e8e8e8;
}

.bdo__gridmocktable {
    overflow-x: auto;
    overflow-y: auto;
    // height: calc(100vh - 300px)!important;
    min-height: 200px;
    .dragable {
        cursor: move;
    }
}

.bdo__gridmocktable--container {
    display: inline-block;
    white-space: nowrap;
    // overflow: auto;
    margin: 0;
    padding: 0;

    &.border {
        border: 1px solid #e8e8e8;
    }
}

.bdo__gridmocktable--block {
    display: inline-block;
    vertical-align: top;
    box-sizing: border-box;
    cursor: default;

    &.active {
        border: 1px solid #409eff !important;
        // background-color: #f2f6fc;
    }

    &.border {
        border-right: 1px solid #e8e8e8;
        border-left: 1px solid #e8e8e8;
    }

    &:last-child {
        border-right: none;
    }
    &:first-child {
        border-left: none;
    }
}

.bdo__gridmocktable--block-table {
    box-sizing: border-box;

    .block-header {
        background-color: #fafafa;
        box-sizing: border-box;
        th {
            color: #000000d9;
            font-weight: 500;
        }

        .block-header-row {
            height: 32px;
            &.border {
                border-bottom: 1px solid #e8e8e8;
            }
        }
    }
}

.bdo__gridmocktable--block-wrapper {
}

.bdo__gridmocktable--row {
    &.border {
        border-bottom: 1px solid #e8e8e8;
    }

    &:last-child {
        border: none;
    }
}

.bdo__gridmocktable--header-column {
    padding: 0 5px;
    box-sizing: border-box;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
    &.border {
        border-right: 1px solid #e8e8e8;
    }

    &:last-child {
        border: none;
    }
}

.bdo__gridmocktable--column {
    padding: 0 5px;
    box-sizing: border-box;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    &.border {
        border-right: 1px solid #e8e8e8;
    }

    &:last-child {
        border: none;
        z-index: 1020;
    }
}

.bdo__gridmocktable--item-hover {
    background-color: #f2f6fc;
    border: 1px solid #409eff !important;
    box-sizing: border-box;
    overflow: hidden;
    z-index: 1020;
}
.deleteRowIcon {
    color: red;
    font-size: 18px;
}
</style>


